@import url("https://fonts.googleapis.com/css2?family=Tomorrow:wght@400;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Assistant:wght@400;800&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-tomorrow: Tomorrow;
  --font-assistant: Assistant;

  /* font sizes */
  --font-size-sm: 14px;
  --font-size-xs: 12px;

  /* Colors */
  --color-white: #fff;
  --color-darkgray-100: #b9aaaa;
  --color-black: #000;
  --color-lightgray: #d8cfcf;

  /* border radiuses */
  --br-5xs: 8px;
}
